<template xmlns="">
  <el-row>
    <!--左侧-->
    <el-col :span="8" style="padding-right: 10px">
      <el-card class="box-card">
        <div class="user">
          <img alt="d" src="../assets/wanfeng.jpg">
          <div class="userinfo">
            <p class="name">{{ userInfo.name }}</p>
            <p class="access">{{ city }}</p>
          </div>
        </div>
        <div class="login-info">
          <p>登录时间:<span>{{ userInfo.time }}</span></p>
          <p>登录地点:<span>{{ city }}</span></p>
        </div>
      </el-card>
      <el-card style="margin-top: 8px;height: 450px;">
        <weibo/>
      </el-card>
    </el-col>
    <!--右侧-->
    <el-col :span="16" style="padding-left: 10px">
      <div class="num">
        <el-card v-for="item in cardData" :key="item.id" :body-style="{display:'flex', padding:0}" shadow="hover">
          <i :class="`el-icon-${item.icon}`" :style="{background: item.color,}" class="icon" @click="handleIconClick(item)"></i>
          <div class="detail">
            <p class="content">{{ item.content }}</p>
            <p class="title">{{ item.title }}</p>
          </div>
        </el-card>
      </div>
      <el-card style="height: 280px">
        <!--折线图-->
        <div ref="echarts1" style="width: 800px; height: 300px" @click="weightVisible=true"></div>
      </el-card>
      <div class="graph">
        <el-card style="height: 260px">待使用</el-card>
        <el-card style="height: 260px">待使用</el-card>
      </div>
    </el-col>
    <el-dialog :visible.sync="CardDialogVisible" title="修改卡片" width="30%">
      <el-form ref="form" :model="curUpdateCard" label-width="80px">
        <el-form-item label="标题">
        <el-input v-model="curUpdateCard.title"></el-input>
        </el-form-item>
        <el-form-item label="图标">
          <el-input v-model="curUpdateCard.icon"></el-input>
        </el-form-item>
        <el-form-item label="颜色">
          <el-color-picker v-model="curUpdateCard.color"></el-color-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="CardDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleSubmit">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="weightVisible" title="添加体重数据" width="30%">
      <el-form ref="form" :model="weightInsertData" label-width="80px">
        <el-form-item label="体重">
          <el-input v-model="weightInsertData.weight"></el-input>
        </el-form-item>
        <el-form-item label="是否锻炼">
          <el-radio-group v-model="weightInsertData.isExec">
            <el-radio label="true"></el-radio>
            <el-radio label="false"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="weightVisible = false;this.weightInsertData={}">取 消</el-button>
        <el-button type="primary" @click="handleWeightSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </el-row>



</template>
<script>
import {addWeight, getHomeData, getLocal, updateCart} from '@/api'
import weibo from "@/components/weibo.vue";
import * as echarts from 'echarts'
let  echarts1;
export default {
  data() {
    return {
      userInfo: {},
      city: '',
      cardData: [],
      weightData: [],
      CardDialogVisible:false,
      curUpdateCard:{},
      weightVisible:false,
      weightInsertData:{},

    }
  },
  mounted() {
    getPublicIP(data => {
      // 获取登陆地址
      getLocal(data).then(data => {
        let local = data.data.data
        this.city = local.province + "-" + local.city
      })
      // 获取主页信息
      getHomeData().then((data) => {
        this.userInfo = data.data.data.userInfo
        this.cardData = data.data.data.cardData
        this.weightData = data.data.data.weightData
        this.userInfo.time = formatDateTime(this.userInfo.time)
        this.fetchCharts1(true)
      })
    })
  },
  components: {
    weibo
  },
  methods: {
    handleWeightSubmit(){
      if (this.weightInsertData.weight === undefined || this.weightInsertData.isExec === undefined){
        this.$message.error("请输入完整数据");
        this.weightVisible = false;
        return
      }
      addWeight(this.weightInsertData).then(data => {
        if (data.data.data){
          this.$message.success(data.data.message)
          this.weightVisible = false;
          this.weightData.push(this.weightInsertData)
          this.weightInsertData = {}
          this.fetchCharts1(false)
        }else {
          this.$message.error(data.data.message)
        }
      })
    },
    fetchCharts1(isInit) {
      if (isInit){
        echarts1 = echarts.init(this.$refs.echarts1)
      }
      var xDataArr = this.weightData.map(item => {
        return formatDate(item.time)
      });
      // 3. 准备y轴数据
      var yDataArr = this.weightData.map(item => parseFloat(item.weight));
      // 4. 准备配置项
      var option = {
        title: {
          text: '体重变化趋势',
          x: 'center',
        },
        xAxis: {
          data: xDataArr,
          axisLabel:{
            interval: 0,
          }
        },
        yAxis: {
          min: 210,
          max: 230,
          type: 'value'
        },
        series: [
          {
            type: 'line',
            data: yDataArr
          }
        ]
      }
      echarts1.setOption(option)
    },
    handleIconClick(item){
      this.curUpdateCard = item;
      this.CardDialogVisible = true;
    },
    handleSubmit(){
      updateCart(this.curUpdateCard).then(data=>{
        if (data.data.data){
          this.$message.success(data.data.message)
          this.CardDialogVisible = false;
        }else {
          this.$message.error(data.data.message)
        }

      })
    }
  }
}

function formatDateTime(dateString) {
  // 创建Date对象
  const date = new Date(dateString);
  // 获取年、月、日、小时、分钟和秒
  const year = date.getFullYear();
  const month = date.getMonth() + 1; // 月份从0开始，所以要加1
  const day = date.getDate();
  const hours = date.getHours();
  const minutes = date.getMinutes();
  let seconds = date.getSeconds();
  if (seconds < 10) {
    seconds = "0" + seconds;
  }
  // 格式化输出
  return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
}

function getPublicIP(callback) {
  var xhr = new XMLHttpRequest();
  xhr.onreadystatechange = function () {
    if (xhr.readyState === 4 && xhr.status === 200) {
      var response = JSON.parse(xhr.responseText);
      callback(response.ip);
    }
  };
  xhr.open("GET", "https://api.ipify.org?format=json", true);
  xhr.send();
}

function formatDate(dateString) {
  if (dateString === undefined){
    return "刷新后显示！"
  }
  var date = new Date(dateString);
  var month = String(date.getMonth() + 1).padStart(2, '0'); // 转换为两位数，如 "01"
  var day = String(date.getDate()).padStart(2, '0'); // 转换为两位数，如 "01"
  var hours = String(date.getHours()).padStart(2, '0'); // 转换为两位数，如 "01"
  var minutes = String(date.getMinutes()).padStart(2, '0'); // 转换为两位数，如 "01"

  return `${day}`;
}
</script>
